@keyframes scale {
  0% {
    transform: scale(0); }
  1% {
    transform: scale(0); }
  20% {
    transform: scale(0); }
  35% {
    transform: scale(1.4); }
  40% {
    transform: scale(1); }
  50% {
    transform: scale(1.15); }
  65% {
    transform: scale(1); }
  90% {
    transform: scale(1); }
  100% {
    transform: scale(1); } }

@keyframes scaleCircle {
  0% {
    border-width: 90px;
    transform: scale(0); }
  15% {
    border-color: white;
    border-width: 90px; }
  30% {
    border-color: white;
    opacity: 1; }
  35% {
    border-color: red; }
  50% {
    border-width: 0;
    transform: scale(1);
    opacity: 0.3; }
  100% {
    border-width: 0;
    transform: scale(1);
    opacity: 0; } }

.root {
  position: relative;
  width: 200px;
  height: 200px;
  margin: 0 auto;
  margin-bottom: 1em;
  display: flex;
  cursor: pointer; }
  .root .thumb {
    position: relative;
    z-index: 1;
    display: block;
    width: 50%;
    height: 100%;
    margin: auto;
    transform: scale(0);
    cursor: pointer; }
  .root .circle-wrap {
    transform: translate(-50%, -50%);
    position: absolute;
    z-index: 100;
    top: 50%;
    left: 50%;
    pointer-events: none; }
    .root .circle-wrap .circle-lg {
      width: 200px;
      height: 200px;
      border-radius: 50%;
      border-style: solid;
      border-color: black;
      opacity: 0; }
  .root .dots-wrap {
    transform: translate(-50%, -50%) rotate(3deg);
    position: absolute;
    top: 50%;
    left: 50%;
    width: 220px;
    height: 220px;
    pointer-events: none; }
    .root .dots-wrap .dot {
      width: 10px;
      height: 10px;
      position: absolute;
      border-radius: 50%;
      background: #00CCFF;
      margin: 0 auto;
      top: 105px;
      left: 0;
      right: 0;
      opacity: 0; }
      .root .dots-wrap .dot:nth-child(6n+1) {
        background-color: #ff1a1a; }
      .root .dots-wrap .dot:nth-child(6n+2) {
        background-color: #ff9999; }
      .root .dots-wrap .dot:nth-child(6n+3) {
        background-color: #ff1a1a; }
      .root .dots-wrap .dot:nth-child(6n+4) {
        background-color: #ff9999; }
      .root .dots-wrap .dot:nth-child(6n+5) {
        background-color: #ff1a1a; }
      .root .dots-wrap .dot:nth-child(6n+6) {
        background-color: #ff9999; }

.root.expended .thumb {
  animation: scale 0s ease-in-out;
  animation-fill-mode: forwards; }

.root.expended .circle-lg {
  animation: scaleCircle 0s ease-in-out;
  animation-fill-mode: forwards; }

.root.expended .dot--t {
  animation: slideUp 0s ease-in-out;
  animation-fill-mode: forwards; }

